.skillsBlock {
  border: 1px solid rebeccapurple;
  background-color: gold;
  width: 100%;
  min-height: 100vh;
}

.skillsContainer {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.title {
  background-color: white;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  border: 1px solid green;
  width: 100%;
}
